",\r\n    components: {AdminTable},\r\n    methods: {\r\n        ...mapActions(['updateData']),\r\n    },\r\n    mixins: [main],\r\n    data() {\r\n        return {\r\n            columns: ['Контент', 'Доступно ли на бесплатном тарифе?'],\r\n            param: ''\r\n        }\r\n    },\r\n    async created() {\r\n        this.$store.commit('setCurrentRoute', this.$route.path.replace(/(\\/*$)/, \"\"))\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.switchToggle input[type=checkbox] {\r\n    height: 0;\r\n    width: 0;\r\n    visibility: hidden;\r\n    position: absolute;\r\n}\r\n\r\n.switchToggle label {\r\n    cursor: pointer;\r\n    text-indent: -9999px;\r\n    width: 70px;\r\n    max-width: 70px;\r\n    height: 30px;\r\n    background: #dc3545;\r\n    display: block;\r\n    border-radius: 100px;\r\n    position: relative;\r\n}\r\n\r\n.switchToggle label:after {\r\n    content: '';\r\n    position: absolute;\r\n    top: 2px;\r\n    left: 2px;\r\n    width: 26px;\r\n    height: 26px;\r\n    background: #fff;\r\n    border-radius: 90px;\r\n    transition: 0.3s;\r\n}\r\n\r\n.switchToggle input:checked + label, .switchToggle input:checked + input + label {\r\n    background: #28a745;\r\n}\r\n\r\n.switchToggle input + label:before, .switchToggle input + input + label:before {\r\n    content: 'Нет';\r\n    position: absolute;\r\n    top: 3px;\r\n    left: 32px;\r\n    width: 26px;\r\n    height: 26px;\r\n    border-radius: 90px;\r\n    transition: 0.3s;\r\n    text-indent: 0;\r\n    color: #fff;\r\n}\r\n\r\n.switchToggle input:checked + label:before, .switchToggle input:checked + input + label:before {\r\n    content: 'Да';\r\n    position: absolute;\r\n    top: 3px;\r\n    left: 13px;\r\n    width: 26px;\r\n    height: 26px;\r\n    border-radius: 90px;\r\n    transition: 0.3s;\r\n    text-indent: 0;\r\n    color: #fff;\r\n}\r\n\r\n.switchToggle input:checked + label:after, .switchToggle input:checked + input + label:after {\r\n    left: calc(100% - 2px);\r\n    transform: translateX(-100%);\r\n}\r\n\r\n.switchToggle label:active:after {\r\n    width: 60px;\r\n}\r\n\r\n.toggle-switchArea {\r\n    margin: 10px 0 10px 0;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
